body {
    background-color: #000;
}
.hero-detail {
    display: flex;
    flex-direction: row;
}
.hero-img {
    width: 300px;
    height:300px;
    padding: 0 40px;
    padding-bottom: 5px;
    border-radius: 50%;
    margin-top: 30%;
}

.detail {
    display: flex;
    flex-direction: column;
    line-height: 1.6;
    padding: 50px;
}
.detail-name {
    font-size: 64px;
    letter-spacing: 1px;
    padding: 5px;
    text-align: center;
}
.detail-specialty,
.detail-race,
.detail-weapon {
    font-size: 20px;
    margin-top: 5px;
}
.detail-span {
    font-size: 15px;
}
.detail-description {
    padding: 10px;
    letter-spacing: .9px;
}
.button-container {
    padding-top: 30px;
    display: flex;
    justify-content: space-evenly;
    letter-spacing: 5px;
    font-weight: 700;
}
.edit-link,
.delete-link {
    text-align: center;
    border: 2px solid #738a8a;
    padding: 5px;
}

@media (max-width: 1024px) {
    .name {
        font-size: 32px;
    }
    .hero-image {
        width: 500px;
        margin-top: -100px;
        padding: 10px;
    }
    .hero-detail {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

 @media (max-width: 800px) {
    .hero-image {
        width: 300px;
        margin: 15px;
        padding: 10px;
    }
    .hero-detail {
        padding: 0px;
        padding-top: 120px;
    }
    .button-container {
        padding: 20px;
        padding-bottom: 80px;
    }
    .edit-link, .delete-link {
        width: 40%;
    }
